body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.description {
  font-size: 12px;
}

form {
  margin: 20px auto;
  /* width: 800px; */
  font-size: 24px;
  width: 500px;
  display: flex;
  align-self: center;
  justify-content: center;
}

button {
  background-color: rgb(150, 149, 223);
  border-radius: 5px;
  color: white;
}

.message {
  width: 500px;
  border: 3px black solid;
  background-color: grey;
  color: white;
  font-size: 20px;
  margin: 0 auto;
  padding: 10px;
  text-align: center;
}

.message.error {
  background-color: rgb(240, 120, 120);
  color: black;
}

.message.success {
  background-color: green;
}

.message.processing {
  background-color: orange;
  color: black;
}

.matches {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.match-container {
  width: 900px;
  display: flex;
  background-color: greenyellow;
  margin-bottom: 10px;
  align-items: center;
}
  
.match-container.Victory {
  background-color: rgb(141, 179, 255);
  background: linear-gradient(to right, #BBBEFF, #164BFF);
  border: blue 1px solid;
}

.match-container.Defeat {
  background-color: rgb(224, 131, 131);
  background: linear-gradient(to right, #FFB4B1, #FF2D2C);
  border: red 1px solid;
}

.game-info {
  text-align: center;
  padding: 10px;
  width: 150px;
}

.outcome {
  font-weight: bold;
}

.player-info {
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  padding: 10px;
  width: 150px;
}

.kda {
  font-weight: bold;
}

.player-item-container {
  display: grid;
  grid-row-gap: 1px;
  grid-column-gap: 2px;
  padding: 10px;
}

.item0-container {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 2;
  border-radius: 5px;
}
.item1-container {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
  border-radius: 5px;
}
.item2-container {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 1;
  grid-row-end: 2;
  border-radius: 5px;
}
.item3-container {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 2;
  grid-row-end: 3;
  border-radius: 5px;
}
.item4-container {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 2;
  grid-row-end: 3;
  border-radius: 5px;
}

.item5-container {
  grid-column-start: 3;
  grid-column-end: 4;
  grid-row-start: 2;
  grid-row-end: 3;
  border-radius: 5px;
}
.item6-container {
  grid-column-start: 4;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 2;
  border-radius: 5px;
  min-width: 30px;
}
.summ1-container {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 3;
  grid-row-end: 4;
  border-radius: 5px;
}
.summ2-container {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;
  border-radius: 5px;
}
.keystone-container {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 4;
  grid-row-end: 5;
  border-radius: 5px;
}
.secondary-container {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 4;
  grid-row-end: 5;
  border-radius: 5px;
}

.players-container {
  display: flex;
  padding: 5px 10px;
}

.blue-team {
  width: 205px;
  display: flex;
  flex-direction: column;
  
}

.red-team-player {
  font-size: 12px;
}

.blue-team-player {
  font-size: 12px;
}